
h1, h2 {
  font-weight: normal;
  margin-left: 5px;
}
a {
  color: #444;
  text-decoration: none;
  cursor: pointer;
}
body {
  font-family: Menlo, Consolas, monospace;
  color: #444;
}
.item {
  margin-left: 10px;
}
.bold {
  font-weight: bold;
}
ul {
  padding-left: 1em;
  line-height: 1.5em;
  list-style-type: dot;
}
.operator {
  cursor: pointer;
}



